<script>
import NavRight from "@/components/pages/navright";
// import NavLeft from "@/components/pages/navleft";
export default {
  name: "Home",
  components: {
    NavRight,
    // NavLeft,
  },
  data() {
    return {
      postsUpade: [],
      nocom: false,
      dialog: false,
      status: true,
      dialogs: false,
      snackbar: false,
      timeout: 2000,
    };
  },
  watch: {
    dialog(val) {
      if (!val) {
        return;
      }
      // setTimeout(() => {
      //   this.dialog = false;
      // }, 5000);
    },
  },
  methods: {
    changePrivacy: async function(idPost) {
      const config = {
        headers: {
          Accept: "application/json,*/*",
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Authorization",
          Authorization: "Bearer " + this.$store.getters.getToken,
        },
      };
      const change = await this.$http.put(
        "/posts/privacy",
        { urlpost: idPost },
        config
      );
      this.snackbar = true;
      this.getData(this.$store.getters.getUserId, this.$store.getters.isAdmin);
      return change;
    },
    getData: async function(user, admin) {
      const config = {
        headers: {
          Accept: "application/json,*/*",
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Authorization",
          Authorization: "Bearer " + this.$store.getters.getToken,
        },
      };

      let urlLink;
      if (admin == 1) {
        urlLink = await this.$http.get("/posts/videos", config);
      } else {
        urlLink = await this.$http.get("/posts/videos/" + user, config);
      }
      if (urlLink.data.length == 0) {
        this.nocom = true;
      } else {
        this.postsUpade = urlLink.data;
      }
    },
  },
  mounted() {
    this.$Progress.start();
    this.getData(this.$store.getters.getUserId, this.$store.getters.isAdmin);
  },
};
</script>
